.contestants-wrapper{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* justify-content: center; */
    -ms-flex-direction: column;
        flex-direction: column;
    padding-right: 2rem;
    padding-left: 2rem;
    /* margin-bottom: 0px; */
    height: 100vh;
    background-color: black;
}

.contestants-wrapper .content{
   /* padding-top: 200px; */
   height: 100vh;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   -ms-flex-direction: column;
       flex-direction: column;
   margin-top: 10rem;
   overflow-x: scroll;
   -webkit-box-sizing: border-box;
           box-sizing: border-box;

}

.contestants-wrapper .single{
    /* padding-top: 200px; */
    /* height: 100vh; */
    /* flex-wrap: wrap; */
    -ms-flex-direction: row;
        flex-direction: row;
    display: -ms-flexbox;
    display: flex;
    height: 100vh;
    overflow-y: hidden;
    /* margin-top: 10rem; */
 }

.single .list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row; 
    /* flex-wrap: wrap; */
    width: 50%;
    /* align-items: center; */
    overflow-y: scroll;
    padding: 4rem;
    padding-top: 15rem;
    padding-left: 0px;
}

.single .view{
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 4rem;
    padding-top: 15rem;
    margin-left: 0px;
    /* height: 100%; */
    overflow-y: scroll;
}

.bio p{
    margin: 0px;
}

.image-wrapper{
    width: 75px;
    height: 75px;
    min-width: 75px;
    margin-right: 4rem; 
    border-radius: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    overflow: hidden;
    background-color:black;
    border: 1px solid rgba(255,255,255,1);
}

.image-wrapper img{
    height: 100%;
    /* width: 100%; */
}

.ordered{
    /* width: 100%; */
    padding-right: 2rem;
    max-width: 400px;
    padding: 4rem;
}

.ordered li {
    margin-bottom: 1.5rem;
}

.ordered li a h3, h1{
    font-size: 28px;
    line-height: 1.35;
}

.letter{
    width: 32px;
    height: 32px;
    border: 1px solid rgba(255,255,255,1);
    text-align: center;
    font-size: 20px;
    margin-bottom: 2rem;
    line-height: 1.8;
}

.single .link{
    text-align: center;
}

.jumplist{
    position: absolute;
    top: 0px;
    left: 0px;
    /* height: 100vh; */
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: black;
    width: 48px;
    border-right: 1px solid rgba(255,255,255, .6);
}

.jumplist .ordered{
    padding: 0px;
}

.jumplist .ordered .letter{
    border: none;
    margin-bottom: 0px;
    cursor: pointer;
    text-decoration: none;
}

.jumplist .ordered .letter:hover{
    border: none;
    margin-bottom: 0px;
    cursor: pointer;
    text-decoration: underline;
}

@media only screen and (max-width: 1024px) {
    .ordered{
        padding: 0px;
        display: -ms-flexbox;
        display: flex;
        text-align: center;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: center;
            justify-content: center;
        max-width: 100%;
    }
    .contestants-wrapper .single{
        -ms-flex-direction: column;
            flex-direction: column;
        display: block;
        overflow: initial;
    }

    .single .list{
        width: 100%;
        height: auto;
        overflow: initial;
        padding: 0px;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
        padding-top: 15rem;
        text-align: center;
    }


    .single .view{
        width: 100%;
        height: auto;
        overflow: initial;
        margin: 0px;
        padding: 0px;
        padding-top: 5rem;
    }

    .image-wrapper{
        margin: 0px;
        margin-bottom: 5rem;
        width: 150px;
        height: 150px;
    }

    .image-wrapper img{
        width: 150px;
        height: 150px;
    }

    .single .view img{
        width: 100%;
        margin-bottom: 5rem;
    }

    .jumplist{
        display: none;
    }

    .contestants-wrapper{
        height: initial;
        padding: 0px;
    }

    .contestants-wrapper .content{
        padding-top: 10rem;
        margin-top: 0px;
        margin: 0 2rem;
    }
}


